import './RulesPage.styl';

import React, {
  Component,
} from 'react';

import PropTypes from 'prop-types';

import {
  Header,
  HeaderBottom,
  BodyRulesList,
  RulesListCompany,
  ApplyBigContainer
} from 'components';

import { bindActionCreators }       from 'redux';
import { connect }                  from 'react-redux';

import {
  CompanyActions
} from 'actions';

@connect(state => ({
  companyData: state.companyReducer
}), dispatch => ({
  companyActions: bindActionCreators(CompanyActions, dispatch)
}))

export default class RulesPage extends Component {
  static propTypes = {
    companyData: PropTypes.object,
    companyActions: PropTypes.object
  };

  constructor(props) {
    super(props);

    this.state = {
      statusAllRules: false,
      checked: false
    };
  }

  changeStatusRule() {
    this.setState({ statusAllRules: !this.state.statusAllRules });
  }

  render() {
    const {
      statusAllRules
    } = this.state;

    const {
      companyData,
      ruleData,
      company
    } = this.props.companyData;

    const {
      companyActions
    } = this.props;

    console.log(company);

    return (
      <div className="c-rules-page">
        <div className="rules-page-container">
          <Header
            indexPage={1}
          />
          <HeaderBottom
            bigTitle="All Rules"
            smallTitle="ALL CAMPAINGS"
            left={true}
            showSearchSmall={true}
            showsSearchBig={true}
            borderBig={company.id && true}
          />
          <div className="rules-body-container">
            <div className="rules-left-container">
              {
                company.id &&
                  <ApplyBigContainer
                    status={statusAllRules}
                    changeStatus={this.changeStatusRule.bind(this)}
                  />
              }
              <BodyRulesList
                rule={ruleData}
                company={company}
                statusAllRules={statusAllRules}
              />
            </div>
            <div className="rules-right-container">
              <RulesListCompany
                data={companyData}
                actions={companyActions}
                company={company}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}



